/* Обнуление */
 
html {
 box-sizing: border-box;
}
*, *::before, *::after {
 box-sizing: inherit;
}
img {
 display: block;
}
h1, h2, h3, h4, h5, h6, p, ul, li, a {
 padding: 0;
 margin: 0;
}
body {
 margin: 0;
 padding: 0;
 background-color: var(--light-color);
}

/* Переменные с цветами */

:root {
 --darck-color: #1C1C1C;
 --white-color: #ffffff;
 --light-color: #F0F0F0;
 --gray-color: #5C5C5C;
}

/* Стили для текста */

.text_h1 {
  font-family: 'CabinetGrotesk';
  font-weight: 900;
  font-size: 62px;
  line-height: 77px;
  letter-spacing: 0%;
  text-align: center;
  text-transform: uppercase;
}
.text_sub-title {
  font-family: 'CabinetGrotesk';
  font-weight: 400;
  font-size: 17px;
  line-height: 24px;
  letter-spacing: 1.18px;
  text-align: center;
}
.text_h2 {
  font-family: 'CabinetGrotesk';
  font-weight: 900;
  font-size: 24px;
  line-height: 30px;
  letter-spacing: 0%;
  text-align: center;
  text-transform: uppercase;
}

.text_h3 {
  font-family: 'WorkSans';
  font-weight: 800;
  font-size: 32px;
  line-height: 38px;
  letter-spacing: 0%;
  text-align: center;
  text-transform: uppercase;
}
.text_p {
  font-family: 'WorkSans';
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0%;
  text-align: center;
} 
.text_a {
  font-family: 'WorkSans';
  font-weight: 400;
  color: var(--darck-color);
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0%;
  text-align: center;
  text-decoration: none;
  transition: 1s;
} 
.text_a:hover {
  font-family: 'WorkSans';
  font-weight: 800;
}
/* Стили для кнопок */
.btn {  
  font-family: 'WorkSans';
  font-weight: 800;
  color: var(--white-color);
  background-color: var(--darck-color);
  border: none;  
  border-radius: 1px;
  width: 100px;
  height: 48px;
  transition: 1s;
}
.btn:hover {
  color: var(--darck-color);
  background-color: var(--white-color);  
  border: solid 1px var(--darck-color);
 }
.btn__transparent {
  font-family: 'WorkSans';
  font-weight: 800;
  font-size: 14px;
  color: var(--darck-color);
  background-color: transparent;
  text-decoration: underline;
  border: none;
  border-radius: 1px;  
  width: 100px;
  height: 48px;
  transition: 2s;
}
.btn__transparent:hover {
  border: solid 1px var(--darck-color);
  background-color: var(--white-color);  
  text-decoration: none;
}

/* Контэйнер */

.container {
 max-width: 1120px;
 margin: 0 auto;
}


/* Header */

.header {
  margin-bottom: 75px;
}
.header__flex {
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  width: 139px;
  height: 17px;
  margin-right: 45px;
}
.header__navmenu {
  width: 550px;
  padding-top: 11px;
  display: flex;
  justify-content: space-between;
  list-style: none;
}
.header__navmenu_item {
  width: 80px;
  height: 30px;
  text-align: center;
}
.header__navmenu_item:last-child {
  margin-right: 0;
}
.header__navmenu_link { 
}
.header__btns {
  display: flex;
}
.header__btn-trprt {
  margin-right: 5px;
}
.header__close {
  display: none;
}
.header__burger {
  display: none;
}

/* Firstscreen */

.firstscreen {
  max-width: 1118px;
  margin: 0 auto;
}
.firstscreen__title {
  margin-bottom: 7px;
}
.firstscreen__sub-title {
  max-width: 900px;
  margin: 0 auto;
  margin-bottom: 36px;
}
.firstscreen__grid_ {
  display: grid;
  grid-template-columns: repeat(3, 2fr);
  gap: 22px;
  margin-bottom: 103px;
}
.firstscreen__grid_card {
  padding-top: 56px;
  border-top: 1px solid var(--darck-color);
  transition: 2s;
}
.firstscreen__grid_card:hover {
  background-color: var(--darck-color);
  color: var(--white-color);
}
.firstscreen__grid_icon {
  filter:url(#filter_B);
  margin: 0 auto 38px;  
}
.firstscreen__grid_title {
  margin-bottom: 13px;
}
.firstscreen__grid_text {
  max-width: 312px;
  margin: 0 auto 25px;
}

/* Worcks */

.works {
}
.works__title {
}
.text_h1 {
}
.works__sub-title {
  max-width: 866px;
  margin: 0 auto 42px;  
}
.works__grid {
  display: grid;
  grid-template-columns: repeat(2, 3fr);
  max-width: 996px;
  margin: 0 auto 100px;
}
.works__card {
  border-bottom: 1px solid var(--darck-color);  
  padding-top: 74px;
  padding-left: 80px;
}
.works__card:nth-child(4n-3), .works__card:nth-child(4n-2){
  padding-top: 99px;
  padding-left: 80px;
}
.works__card:nth-child(4n-3) .works__card_text, .works__card:nth-child(4n-2) .works__card_text {
  margin-bottom: 70px;
}
.works__card:nth-last-of-type(-n+2) {
  border-bottom: none;  
  padding-top: 74px;
}
.works__card:nth-child(odd) {
  border-right: 1px solid var(--darck-color); 
  padding-left: 7px;  
}
.works__card:nth-child(odd) .works__card_title {
  margin-right: 79px;
}

.works__card_img{

}
.works__card_title {
  margin-bottom: 3px;
}
.works__card_text {
  max-width: 412px;
  font-size: 17px;
  line-height: 23px;
  margin-bottom: 71px;
}

/* Mobile menu */
.header__mobile {
  display: none;
}
.header__mobile-menu {
  list-style: none;
  margin-bottom: 70px;
}
.header__mobile-menu_logo_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}
.header__mobile_logo {
  width: 272px;
  height: 34px;
}
.header__close {
}
.header__mobile-menu_item {
  max-width: 200px;
  text-align: center;
  margin: 0 auto 20px;
  border-bottom: solid 1px #dfdede;
  padding-bottom: 10px;
}
.header__mobile-menu_item:hover {
  padding-bottom: 11px;
  border-bottom: none;
}
.header__mobile-menu_link {
}
.text_a {
}
.header__mobile-menulink {
}
.header__mobile-menu_btns {
  text-align: center;
}
.header__mobile-menu_btn-trprt {
}
.btn__transparent {
}
.header__mobile-menu_btn {
}
.btn {
}



@media screen and (max-width: 1300px) {

  /* Контэйнер */

.container {
  max-width: 990px;  
 }

 /* Mobile menu */
 .header__navmenu {
  display: none;
 }
 .header__btns {
  display: none;
 }
 .firstscreen__grid_ {
  grid-template-columns: repeat(2, 3fr);
 }
 .works__grid {
  grid-template-columns: repeat(1, 6fr);
 }
 .works__card {
  border-bottom: 1px solid var(--darck-color);    
  padding-left: 0;
 }
 .works__card:nth-last-of-type(-n+2) {
  border-bottom: 1px solid var(--darck-color);    
 }
 .works__card:nth-last-of-type(-n+1) {
  border-bottom: none;
 }
 .works__card:nth-child(odd) {
  border-right: none;
 }
 .works__card:nth-child(4n-2), .works__card:nth-child(4n-3) {
  padding-left: 0;  
 }
 .works__card:nth-child(odd) {
    padding-left: 0;
 }
.works__card_text {
  margin: 0 auto 70px;
 }
 .works__card:nth-child(odd) .works__card_title {
  margin-right: 0;
}
.works__card_img{
margin: 0 auto;
}
.header__close {
  font-family: 'WorkSans';
  color: var(--darck-color);
  display: block;
  text-decoration: none;
  font-size: 60px;
  line-height: 50px;
  font-weight: 400;
  margin-left: auto;
}
.header__burger {
  display: block;
  margin-left: auto;
  width: 40px;
}
.header__burger_line {
  height: 5px;
  margin-bottom: 5px;
  border-radius: 2px;
  background-color: var(--darck-color);
  }
.header__burger_line:last-child {
  margin-bottom: 0;
  }
}
@media screen and (max-width: 1060px) {
  .container {
    max-width: 860px;  
   }   
}
@media screen and (max-width: 930px) {
  .container {
    max-width: 730px;  
   }
}
@media screen and (max-width: 800px) {
  .container {
    max-width: 600px;  
   }   
}
@media screen and (max-width: 650px) {
  .container {
    max-width: 450px;  
   }   
   .text_h1 {
    font-size: 35px;
  }
  .text_sub-title {
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 0.98px;
  }
  .text_h3 {
    font-size: 25px;
  }
  .text_h2 {
    font-size: 18px;
   }   
  .text_p {
    font-size: 12px;
    line-height: 18px;
    }
  .works__card_img {
    width: 150px;
   }
  .works__card {
    padding-top: 40px;
    }
  .works__card:nth-child(4n-2), .works__card:nth-child(4n-3) {
    padding-top: 40px;
  }
}
@media screen and (max-width: 500px) {
  .container {
    max-width: 300px;  
   }
   .header__mobile-menu_logo_inner {
    flex-direction: column-reverse
   }
   .header__close {
    margin-bottom: 40px;
   }
   .firstscreen__grid_ {
    grid-template-columns: repeat(1, 3fr);
  }
   
}
